body {
	margin-left: auto;
	margin-right: auto;
	max-width: 1100px;
	margin-top: 50px;
}

.card {
	position: relative;
	width: 300px;
	border: 1px solid grey;
	margin-top: 2em;
	overflow: hidden;
}

.card img {
	width: auto;
	height: auto;
	min-width: 100%;
	min-height: 100%;
}

.card div {
	position: absolute;
	left: 0;
	bottom: 0;
	right: 0;
	color: white;
	background: rgba(0, 0, 0, 0.5);
	padding: 15px;
}
